
import * as React from 'react';
import { Grid } from '@material-ui/core';
import { InquiryData, DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faMinusCircle, faPlus, faPen } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { InputChip } from 'Pages/CustomerCarePage/Components/InputChip';
import { CustomChip } from 'Pages/CustomerCarePage/Components/CustomChip';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';

interface IProps {
    inquiries: InquiryData[];
    refreshInquiryState: () => void;
}

interface IStates {
    inquiry: DropdownChoice;
}

export class UpdateInquirySection extends React.Component<IProps, IStates> {

    inquiries: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            inquiry: { value: '', label: '' }
        };

        this.inquiries = [
            { value: '1', label: 'Member Status / Card Inquiry' },
            { value: '2', label: 'Member Network Access Inquiry' },
            { value: '3', label: 'Member Benefit Inquiry - General [Outpatient]' },
            { value: '4', label: 'Member Benefit Inquiry - General [Inpatient]' },
            { value: '5', label: 'Member Benefit Inquiry - General [Emergency]' },
            { value: '6', label: 'Member Benefit Inquiry - General [Dental]' },
            { value: '7', label: 'Member Benefit Inquiry - General [APE / ECU]' },
            { value: '8', label: 'Member Benefit Inquiry - General [Riders]' },
            { value: '9', label: 'Member Coverage Inquiry - Specific Benefit' },
            { value: '10', label: 'Member Availment Inquiry - CanAvail' },
            { value: '11', label: 'Member Eligibility Inquiry' },
            { value: '12', label: 'Reimbursement Status Inquiry' },
            { value: '13', label: 'General Affiliated Network Inquiry - Facility' },
            { value: '14', label: 'General Affiliated Network Inquiry - Doctor' },
            { value: '15', label: 'General Affiliated Network Inquiry - Dentist' },
            { value: '16', label: 'Procedural Inquiry - Outpatient' },
            { value: '17', label: 'Procedural Inquiry - Inpatient' },
            { value: '18', label: 'Procedural Inquiry - Emergency' },
            { value: '19', label: 'Procedural Inquiry - Dental' },
            { value: '20', label: 'Procedural Inquiry - APE / ECU' },
            { value: '21', label: 'Procedural Inquiry - Member Enrolment / Cancellation / Update' },
            { value: '22', label: 'Procedural Inquiry - Reimbursement' },
            { value: '23', label: 'Procedural Inquiry - Others' }
        ];
    }

    getFilteredInquiries(): DropdownChoice[] {
        let filteredInquiries: DropdownChoice[] = [];
        for (var i = 0; i < this.inquiries.length; i++) {
            let isSame = false;
            for (var j = 0; j < this.props.inquiries.length; j++) {
                isSame = (
                    this.props.inquiries[j].inquiry.value === 
                    this.inquiries[i].value ? true : isSame);
            }
            if (!isSame) {
                filteredInquiries = filteredInquiries.concat(this.inquiries[i]);
            }
        }
        return filteredInquiries;
    }

    onAddInquiry() {
        if (this.state.inquiry.value !== '') {
            this.props.inquiries.push({
                inquiry: this.state.inquiry,
                detail: '',
                specificDetail: [],
                answer: '',
                remarks: '',
                relatedTickets: [],
                supportDocs: []
            });
            
            this.props.refreshInquiryState();
            this.setState({inquiry: { value: '', label: '' }});
        }
    }

    onRemoveInquiry(index: number) {
        this.props.inquiries.splice(index, 1);
        this.props.refreshInquiryState();
    }


    render(): JSX.Element {
        return (
            <div className='update-ticket'>
                <div className='section' style={{paddingRight: '2.5rem'}}>
                    <div className='header-1'>Update Inquiry</div>
                    <div className='text'>
                        Add an inquiry type and then add the question and its details after
                    </div>
                    <div className='sub-section'>
                        {true ? (
                            <div />
                            // <ComplexTable 
                            //     data={this.props.inquiries}
                            //     renderHeader={() => this.renderTableHeader()}
                            //     renderRow={(rowData: any, index: number) => 
                            //         this.renderTableRow(rowData, index)} />
                        ) : (null)}
                    </div>
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
                <Grid item xs={10}>
                    <Dropdown 
                        choices={this.getFilteredInquiries()}
                        value={this.state.inquiry}
                        onChange={(value: DropdownChoice) => {
                            this.setState({inquiry: value});
                        }} />
                </Grid>
                <Grid item xs={2}>
                    <div className='sub-section' style={{textAlign: 'center'}}>
                        <TextButton 
                            icon={faPlus} 
                            label="Add inquiry"
                            onClick={() => {
                                this.onAddInquiry();
                            }} />
                    </div>
                    
                </Grid>
            </>
        );
    }

    renderTableRow(rowData: any, index: number): JSX.Element {
        return (
            <>
                <div className='small-section'>
                    <Grid container spacing={1}>
                        <Grid item xs={10} className='header-2'>
                            {rowData.inquiry.label}
                        </Grid>
                        <Grid item xs={2} style={{textAlign: 'right'}}>
                            <TextButton
                                icon={faPen}
                                onClick={() => {
                                    this.onRemoveInquiry(index);
                                }}/>
                            <TextButton
                                icon={faMinusCircle}
                                onClick={() => {
                                    this.onRemoveInquiry(index);
                                }}/>
                        </Grid>
                    </Grid>
                </div>
                <div className='line-border' />
                <div className='section'>
                    <Grid container spacing={2}>
                        <Grid item xs={5}>
                            <InputField 
                                placeholder='Details of inquiry (Question)'
                                value={rowData.detail}
                                onChange={(value: string) => {
                                    rowData.detail = value;
                                    this.props.refreshInquiryState();
                                }} />
                        </Grid>
                        <Grid item xs={7}> 
                            <InputChip 
                                placeholder='Specific Details (Type benefit/doctor/facility to describe inquiry, enter to add)'
                                value={rowData.specificDetail}
                                onAdd={(value: string) => {
                                    rowData.specificDetail.push(value);
                                    this.props.refreshInquiryState();
                                }}
                                onDelete={(index: number) => {
                                    rowData.specificDetail.splice(index, 1);
                                    this.props.refreshInquiryState();
                                }} />
                        </Grid>
                    </Grid>
                    <InputField 
                        placeholder='Answer'
                        value={rowData.answer}
                        onChange={(value: string) => {
                            rowData.answer = value;
                            this.props.refreshInquiryState();
                        }} />
                    <InputField 
                        placeholder='Remarks (Optional)'
                        value={rowData.remarks}
                        onChange={(value: string) => {
                            rowData.remarks = value;
                            this.props.refreshInquiryState();
                        }} />
                    <Grid container spacing={2}>
                        <Grid item xs={5}>
                            <InputChip 
                                placeholder='Related Tickets (Optional)'
                                value={rowData.relatedTickets}
                                onAdd={(value: string) => {
                                    rowData.relatedTickets.push(value);
                                    this.props.refreshInquiryState();
                                }}
                                onDelete={(index: number) => {
                                    rowData.relatedTickets.splice(index, 1);
                                    this.props.refreshInquiryState();
                                }} />
                        </Grid>
                        <Grid item xs={4}>
                            <CustomDropzone
                                placeholder='Supporting Documents'
                                onDrop={(files: File[]) => {
                                    rowData.supportDocs.push(files[0]);
                                    this.props.refreshInquiryState();
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            {rowData.supportDocs.map((doc, index) => {
                                return (
                                    <CustomChip
                                        label={doc.name}
                                        value={index.toString()}
                                        onDelete={(value: string) => {
                                            rowData.supportDocs.splice(parseInt(value), 1);
                                            this.props.refreshInquiryState();
                                        }} />
                                );
                            })}
                        </Grid>
                    </Grid>
                </div>
            </>
        );
    }
}