import * as React from 'react';
import { Button } from '@material-ui/core';

interface IProps {
    onSubmitTicket: () => void;
    onSaveForNow: () => void;
}

export class SaveTicketFooter extends React.Component<IProps> {

    shouldComponentUpdate(_props: IProps, _state: any) {
        return false;
    }

    render(): JSX.Element {
        return (
            <div className='footer'>
                <Button className='button-transparent'>
                        Save For Now
                </Button>
                <Button 
                    className='button'
                    onClick={this.props.onSubmitTicket}>
                    Submit Ticket
                </Button>
                
            </div>
        );
    }
}